:root {
  --navhover-color: #00acdc;
  --header-height: 60px;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

body {
  margin: 0;
  padding: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  height: 40px;
}

.nav {
  align-items: center;
  display: flex;
}

.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-item {
  display: flex;
}

.nav-item + .nav-item {
  margin-left: 20px;
}

.nav-link {
  text-transform: uppercase;
  text-align: center;
  height: var(--header-height);
  font-weight: 500;
  line-height: var(--header-height);
  text-decoration: none;
  position: relative;
}

.nav-link:link {
  color: #000;
}

.nav-link:hover {
  color: var(--navhover-color);
}

.nav-link.is-active {
  color: var(--navhover-color);
  display: block;
}

.nav-link.is-active:before {
  content: "";
  background-color: var(--navhover-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
